import { Component } from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  start = false;
  maxLevel = 1;
  level = 1;
  a = 0;
  b = 0;
  result = '';
  decimal = false;
  point = 0;
  gameTime = 15;
  countDownTime = this.gameTime;
  interval: any;

  constructor() { }

  startGame() {
    if (this.start === false) {
      this.start = true;
    }

    this.level = 1;
    this.a = 0;
    this.b = 0;
    this.point = 0;
    this.countDownTime = this.gameTime;

    this.startCountdown();
    this.plus();
  }

  plus() {
    const critLevel = (this.level + 1) * 5;
    this.a = this.randomNumber(critLevel);
    this.b = this.randomNumber(critLevel);
    if (this.a === 0 || this.b === 0 || this.a + this.b < critLevel - 1) {
      this.plus();
    }
  }

  chooseNumber(key: any) {
    if (key === '-') {
      if (this.result === '') {
        this.result += key;
      }
    } else if (key === '.') {
      if (this.decimal === false && this.result !== '') {
        this.result += key;
        this.decimal = true;
      }
    } else {
      this.result += key;
    }
  }

  clearResult() {
    this.result = '';
    this.decimal = false;
  }

  uplevel() {
    this.level++;
    if (this.maxLevel <= this.level) {
      this.maxLevel = this.level;
    }
  }

  checkResult() {
    const sum = this.a + this.b;
    if (Number(this.result) === sum) {
      this.countDownTime += Math.ceil(this.countDownTime / 10) + 2;
      if (this.countDownTime > 30) {
        this.countDownTime = 30;
      }
      this.clearResult();
      this.plus();
      this.uplevel();
    } else {
      this.clearResult();
    }
  }

  randomNumber(size: any) {
    return Math.floor(Math.random() * size);
  }

  startCountdown() {
    this.interval = setInterval(() => {
      this.countDownTime--;
      if (this.countDownTime < 0) {
        clearInterval(this.interval);
        this.start = false;
        this.countDownTime = this.gameTime;
      }
    }, 1000);
  }
}
